.zantimes-post-by-category {
  padding-top: 20px;
  padding-bottom: 20px;
}
.zantimes-post-by-category__title {
  color: #1D0056;
}
.zantimes-post-by-category__title a {
  font-weight: bold;
  text-decoration: none;
  color: #1D0056;
}
.zantimes-post-by-category__title a img {
  margin-left: 10px;
}
.zantimes-post-by-category .featured-img {
  object-fit: cover;
  object-position: left center;
  width: 100%;
  border-radius: 8px;
}
.zantimes-post-by-category .bigger .featured-img {
  height: auto;
}
.zantimes-post-by-category .smaller {
  margin-bottom: 1.25em;
}
.zantimes-post-by-category .smaller .featured-img {
  height: auto;
  margin-right: 1.25em;
}
.zantimes-post-by-category .post-title {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.zantimes-post-by-category .post-link {
  text-decoration: none;
  color: #000000;
}
.zantimes-post-by-category .post-excerpt a {
  text-decoration: none;
  font-weight: bold;
}
@media screen and (min-width: 992px) {
  .zantimes-post-by-category .bigger .featured-img {
    height: auto;
  }
  .zantimes-post-by-category .smaller .featured-img {
    width: 270px;
    height: auto;
  }
  .zantimes-post-by-category .smaller .post-title {
    max-width: 200px;
  }
}
@media (min-width: 992px) and (max-width: 1199px) {
  .zantimes-post-by-category .smaller .featured-img {
    width: 150px;
    height: auto;
  }
}
@media screen and (max-width: 991px) {
  .zantimes-post-by-category .bigger {
    margin-bottom: 1rem;
  }
  .zantimes-post-by-category .featured-img {
    margin-bottom: 1rem;
  }
  .zantimes-post-by-category .smaller .featured-img {
    width: 300px;
  }
}
@media screen and (max-width: 767px) {
  .zantimes-post-by-category__title {
    margin-bottom: 1.5rem;
  }
  .zantimes-post-by-category .smaller .featured-img {
    width: 100%;
    padding-right: 0;
  }
  .zantimes-post-by-category .post-excerpt a {
    display: block;
  }
}